import React, { useState } from 'react';
import PhoneInput from 'react-phone-input-2';
import 'react-phone-input-2/lib/style.css';
import './login.css';
import axios from 'axios';

const SignUpWithPhone = ({ onLoginSuccess }) => {
  const [phoneNumber, setPhoneNumber] = useState('');
  const [lastName, setLastName] = useState('');
  const [otpSent, setOtpSent] = useState(false);
  const [otp, setOtp] = useState('');
  const [message, setMessage] = useState(''); //   
  const [loading, setLoading] = useState(false); //    

  //    OTP
  const handleSendOtp = async () => {
    if (phoneNumber && lastName) {
      setLoading(true); //    
      try {
        const response = await axios.post('/api/send-otp', { phoneNumber, lastName });
        setOtpSent(true); //      otpSent  
        setMessage(response.data.message); //     
      } catch (error) {
        console.error('Error sending OTP:', error);
        setMessage(error.response?.data?.message || '    .'); //   
      } finally {
        setLoading(false); //    
      }
    } else {
      setMessage('        .');
    }
  };

  //    OTP
  const handleVerifyOtp = async () => {
    if (!otp) {
      setMessage('      .');
      return;
    }

    if (!/^\d{6}$/.test(otp)) { //    OTP  6  
      setMessage('    6  .');
      return;
    }

    setLoading(true); //    
    try {
      const response = await axios.post('/api/verify-otp', { phoneNumber, otp }); //     OTP
      setMessage(response.data.message); //      
      onLoginSuccess(); //         
    } catch (error) {
      console.error('Error verifying OTP:', error); //    
      setMessage(error.response?.data?.message || '    .'); //       
    } finally {
      setLoading(false); //        
    }
  };

  return (
    <div className="signup-container">
      <div className="form-box">
        <h2>{otpSent ? '   ' : ' '}</h2>
        {message && <p className="message">{message}</p>} {/*   */}
        {!otpSent ? (
          <>
            <input
              type="text"
              placeholder=" "
              value={lastName}
              onChange={(e) => setLastName(e.target.value)}
              required
            />
            <PhoneInput
              country={'ir'}
              value={phoneNumber}
              onChange={(value) => setPhoneNumber(value)}
              inputStyle={{
                width: '100%',
                borderRadius: '5px',
                borderColor: '#ccc',
                height: '40px',
              }}
            />
            <button onClick={handleSendOtp} disabled={loading}>
              {loading ? '  ...' : ''}
            </button>
          </>
        ) : (
          <>
            <input
              type="text"
              placeholder="  "
              value={otp}
              onChange={(e) => setOtp(e.target.value)}
              required
            />
            <button onClick={handleVerifyOtp} disabled={loading}>
              {loading ? '  ...' : ''}
            </button>
          </>
        )}
      </div>
    </div>
  );
};

export default SignUpWithPhone;
