import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import axios from 'axios';
import "./PaymentPage.css";

const PaymentPage = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [amount, setAmount] = useState(0);

  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  const price = queryParams.get('price');

  useEffect(() => {
    if (price) {
      setAmount(parseInt(price, 10));
    }
  }, [price]);

  const handleSubmit = async (event) => {
    event.preventDefault();

    try {
      const response = await axios.post('https://api.zibal.ir/v1/request', {
        merchant: 'aa437cc147d74e0c881896462f67fe1b', //  merchant 
        amount: amount * 10, //    
        callbackUrl: 'http://yourwebsite.com/callback', //  
        description: '   ',
        mobile: phone,
        email: email
      });

      if (response.data.result === 100) {
        const trackId = response.data.trackId;
        //  
        window.location.href = `https://gateway.zibal.ir/start/${trackId}`;
      } else {
        console.error('    :', response.data.message);
      }
    } catch (error) {
      console.error('   :', error);
    }
  };

  return (
    <div className="payment-page">
      <h1> </h1>
      <form onSubmit={handleSubmit}>
        <label>
             :
          <input type="text" value={name} onChange={(e) => setName(e.target.value)} required />
        </label>
        <label>
           :
          <input type="email" value={email} onChange={(e) => setEmail(e.target.value)} required />
        </label>
        <label>
           :
          <input type="text" value={phone} onChange={(e) => setPhone(e.target.value)} required />
        </label>
        <label>
           :
          <input type="number" value={amount} readOnly />
        </label>
        <button type="submit"></button>
      </form>
    </div>
  );
};

export default PaymentPage;
